import * as React from 'react';

export interface Props {
  src: string;
  className?: string;
  alt?: string;
  style?: any;
}

const imageStyles = {
  maxWidth: '100%',
  marginLeft: 'auto',
  marginRight: 'auto',
  display: 'block',
};

function Image(props: Props) {
  const { src } = props;
  if (!src) {
    throw new Error("You can't render an empty image!");
  }

  if (src.startsWith('/') || src.startsWith('http://') || src.startsWith('https://')) {
    return <img className={props.className} src={src} alt={props.alt || ''} style={{ ...imageStyles, ...props.style }}></img>;
  } else {
    throw new Error('Not supported yet.');
  }
}

export default Image;
