import React from 'react';

import { DefaultColorPalette } from '@mui/joy/styles/types/colorSystem';
import Chip from '@mui/joy/Chip';

import Link from '../components/Link';

import * as style from './Tag.module.less';

interface TagProps {
  name: string;
  color?: DefaultColorPalette;
  size?: 'md' | 'sm' | 'lg';
  sx?: any;
}

export default (function Tag(props) {
  return (
    <Link to={`/tag/${props.name}`} className={style.tag}>
      <Chip className={style.tagChip} sx={props.sx} variant="outlined" color={props.color || 'neutral'} size={props.size || 'sm'} onClick={() => {}}>
        {props.name}
      </Chip>
    </Link>
  );
} as React.FC<TagProps>);
