import React, { useState, useEffect } from 'react';
import { graphql, PageProps } from 'gatsby';
import blogData from '../data/blogData';

import Box from '@mui/joy/Box';
import Avatar from '@mui/joy/Avatar';
import Button from '@mui/joy/Button';
import IconButton from '@mui/joy/IconButton';
import JoyTypography from '@mui/joy/Typography';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';

import AppContainer from '../layouts/AppContainer';
import Tag from '../components/Tag';
import Image from '../components/Image';
import Typography from '../components/Typography';

import * as style from './BlogPost.module.less';

export default function BlogPostTemplate({ data }: PageProps<Queries.BlogPostTemplateQuery>) {
  const { frontmatter, html } = data.markdownRemark;
  const { title, date, cover, tags } = frontmatter;

  const getCoverWidth = () => {
    const width = document.body.clientWidth;
    return width >= 900 ? width - 300 : width;
  };
  const [coverWidth, setCoverWidth] = useState(0);

  useEffect(() => {
    const handleResize = () => setCoverWidth(getCoverWidth());
    handleResize();
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <AppContainer title={title!} isArticle={true} isHeaderFloating={true}>
      {frontmatter?.cover && (
        <Box style={{ position: 'relative' }}>
          <Box className={style.blogPostCover} style={{ width: `${coverWidth}px`, marginLeft: `calc(50% - ${coverWidth / 2}px)` }}>
            <Image className={style.blogPostCoverImage} src={cover!} style={{ width: '100%' }}></Image>
          </Box>
        </Box>
      )}
      <Box>
        <Box className={style.blogPostCard}>
          <Box className={style.blogPostCardBody}>
            <h1 className={style.blogPostTitle}>{title!}</h1>
            <Box className={style.blogPostBox} sx={{ gap: 2 }}>
              <Box className={style.blogPostBoxAvatar}>
                <Avatar src={blogData.avatarImgSmall} srcSet={blogData.avatarImg} sx={{ borderRadius: 'sm' }} />
                <Box sx={{ ml: 2 }}>
                  <JoyTypography level="body2" textColor="text.primary" mb={0.5}>
                    {blogData.avatar}
                  </JoyTypography>
                  <JoyTypography level="body3" textColor="text.tertiary">
                    {date!}
                  </JoyTypography>
                </Box>
              </Box>
              <Box className={style.blogPostBoxIcon} sx={{ gap: 1.5 }}>
                <IconButton size="sm" variant="outlined" color="neutral">
                  <FavoriteBorderIcon />
                </IconButton>
              </Box>
            </Box>

            <Typography sx={{ mt: 4 }}>
              <div dangerouslySetInnerHTML={{ __html: html! }}></div>
            </Typography>

            {tags?.length && <Box sx={{ mt: 2 }}>{tags.map((name: string | null, index: number) => name && <Tag name={name} size="md" sx={{ ml: index ? 1 : 0 }}></Tag>)}</Box>}
          </Box>
        </Box>
      </Box>
    </AppContainer>
  );
}

export const pageQuery = graphql`
  query BlogPostTemplate($id: String!) {
    markdownRemark(id: { eq: $id }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        title
        cover
        tags
      }
    }
  }
`;
